.about-us {
  @include pageGrid;

  &__title {
    grid-column: 1/-1;

    @include onTablet {
      grid-column: 1/4;
    }

    @include onDesktop {
      grid-column: span 6;
    }
  }

  &__content {
    margin: 0;
    grid-column: 1/-1;

    @include onTablet {
      grid-column: 4/7;
    }

    @include onDesktop {
      grid-column: 8/13;
    }
  }

  &__description {
    margin: 0;
    line-height: 140%;
    letter-spacing: 0.15px;
  }
}
