.icon {
  display: block;
  height: 24px;
  width: 24px;
  background-size: cover;
  background-position: center;

  @include hover(transform, scale(1.2));

  @include onTablet {
    height: 32px;
    width: 32px;
  }

  &--phone {
    background-image: url(../Icons/icon-phone.svg);
    text-decoration: none;
    display: flex;
    align-items: center;

    @include onDesktop() {
      &:hover::before {
        content: "+1 234 555-55-55";
        margin-left: -145px;
        padding-right: 5px;
        color: $main-text-color;
        text-align: right;
        font-family: Inter;
        font-size: 12px;
        font-style: normal;
        font-weight: 700;
        line-height: 16px;
        letter-spacing: 2px;
        text-transform: uppercase;
      }
    }
  }

  &--menu {
    background-image: url(../Icons/icon-burger-menu.svg);

    &:hover {
      background-image: url(../Icons/icon-burger-menu-hover.svg);
    }
  }

  &--close {
    background-image: url(../Icons/icon-close.svg);
  }
}
