.product {
  &__photo {
    width: 100%;

    @include hover(transform, scale(1.05));
  }

  &__title {
    font-size: 18px;
    font-weight: 700;
    line-height: 25px;
  }

  &__category {
    font-weight: 400;
    line-height: 22px;
    letter-spacing: 0.15;
  }

  &__price {
    font-weight: 700;
    line-height: 22px;
  }
}
